<template>
    <div class='debug-wrapper'>
        <table>
            <tr v-for="(variable,index) in variables" :key="index">
                <td>{{variable}}</td>
                <td><pre>{{getVarValue(variable)}}</pre></td>
            </tr>
        </table>
    </div>
</template>

<script>
import {mapState,mapGetters,mapMutations,mapActions} from 'vuex'
export default {
    panelTitle: 'Debug',
    data() {
        return {
            variables: ['getIsTimerRunning','getCurrentStepBuffer','getMaxStepBuffer','getStepInterval']
        }
    },
    computed:{
        ...mapGetters('wizard',['getConfiguration']),
        ...mapGetters('dashboard', ['getIsTimerRunning','getCurrentStepBuffer','getMaxStepBuffer','getStepInterval']),
    },
    methods:{
      getVarValue: function (varName) {
          //console.log(varName, 'called')
          return this[varName]
      }
    }
}
</script>


